import React from 'react';
import './Card.css';
const Card = ({ name, email, id }) => {
  return (
    <div className="tc bg-light-green dib br3 ma2 pa3 grow shadow-5">
      <img src={`https://robohash.org/${id}?size=200x200`} alt="robots" />
      <div>
        <h2>{name}</h2>
        <p>{email}</p>
      </div>
    </div>
  );
};

export default Card;
